import {Component} from "@angular/core";
import {App, NavController, NavParams} from "ionic-angular";
import {Preview} from "../../models/preview";
import {ServiceProvider} from "../../providers/service/service";
import {environment} from "../../environments/environment";
import {DetailsPage} from "../details/details";

/**
 * Generated class for the PreviewPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */
@Component({
  selector: 'page-preview',
  templateUrl: 'preview.html',
})
export class PreviewPage {
  previewUrl: string;
  previewId: string;
  errorMessage: string;
  preview: Preview;

  constructor(private app: App, public navCtrl: NavController, public navParams: NavParams, private httpService: ServiceProvider) {
    console.log(this.navParams.data);
    this.previewId = this.navParams.data;

    this.preview = new Preview();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad VideoPage');
  }

  ngOnInit(): void {
    console.log('ngOnInit VideoPage');
    let url = (environment.production) ? `/ionic-education/preview` : `/assets/mock/preview.json`;

    this.httpService.get(url).subscribe(
      preview => {
        this.preview = preview;
        // https://view.officeapps.live.com/op/view.aspx?src=
        this.previewUrl = "https://view.officeapps.live.com/op/view.aspx?src=" + this.preview.url;
        console.log("subscribe: previewUrl = " + this.previewUrl);
      },
      error => {
        this.errorMessage = <any>error;
      }
    );
  }

  gotoAppDownload() {
    console.log("gotoAppDownload");
  }

  gotoDetailsPage() {
    this.entryDetailsPage(this.preview.url);
  }

  private entryDetailsPage(url: string) {
    this.app.getRootNav().push(DetailsPage, url);
  }
}



