import {Component, OnInit} from "@angular/core";
import {NavController, NavParams} from "ionic-angular";
import {Video} from "../../models/video";
import {VideoServiceProvider} from "../../providers/video-service/video-service";

/**
 * Generated class for the VideoPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-video',
  templateUrl: 'video.html',
})
export class VideoPage implements OnInit {
  videoId: string;
  errorMessage: string;
  video: Video;

  constructor(public navCtrl: NavController, public navParams: NavParams, private videoService: VideoServiceProvider) {
    console.log(this.navParams.data);
    this.videoId = this.navParams.data;

    this.video = new Video();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad VideoPage');
  }

  ngOnInit(): void {
    console.log('ngOnInit VideoPage');
    this.videoService.getVideo(this.videoId).subscribe(
      video => {
        this.video = video;
        var videoPlayer = document.getElementById("video-player");
        videoPlayer.setAttribute('src', this.video.url);
        console.log("subscribe")
      },
      error => {
        this.errorMessage = <any>error;
      }
    );
  }

  gotoAppDownload() {
    console.log("gotoAppDownload");
  }
}



