import {Injectable} from "@angular/core";
import {Http, Response} from "@angular/http";
import {Video} from "../../models/video";
import {Observable} from "rxjs/Observable";
import "rxjs/add/operator/map";
import "rxjs/add/operator/catch";
import {environment} from "../../environments/environment";
import {LoadingController} from "ionic-angular";

/*
 Generated class for the VideoServiceProvider provider.

 See https://angular.io/guide/dependency-injection for more info on providers
 and Angular DI.
 */
@Injectable()
export class VideoServiceProvider {

  constructor(public http: Http, public loadingCtrl: LoadingController) {
    console.log('Hello VideoServiceProvider Provider');
  }

  getVideo(id: string): Observable<Video> {
    let url = `/assets/mock/video.json`;  // 本机模拟请求

    if (environment.production) {
      url = `/ionic-education/video`;  // 跨域访问 ionic.config.json proxyUrl
    }
    console.log('VideoServiceProvider = ' + url);

    // 加载进度条
    const loading = this.loadingCtrl.create({
      content: 'Please wait...',
      dismissOnPageChange: true
    });
    loading.present();

    return this.http.get(url)
      .map(
        response => {
          loading.dismiss();
          let body = response.json();
          console.log('response = ' + JSON.stringify(body));
          return body.data || {};
        }
      ).catch(
        error => {
          loading.dismiss();
          let errMsg: string;
          if (error instanceof Response) {
            const body = error.json() || '';
            const err = body.error || JSON.stringify(body);
            errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
          } else {
            errMsg = error.message ? error.message : error.toString();
          }
          console.error(errMsg);
          return Observable.throw(errMsg);
        }
      );
  }
}



